import {
  AssignUserModalSkeleton,
  AssignUserSelectSkeleton,
  AssignUserSelect,
  UserList,
} from '@components/OrgRole/Users';
import ErrorReport from '@components/ui/ErrorReport';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { defer } from '@remix-run/node';
import { Await, useLoaderData } from '@remix-run/react';
import { getOrgBeingViewed } from '@routes/_withNav+/_withSideBar+/_manage+/.server/org.queries';
import { requireUserAsObject } from '@services/user.server';
import React, { Suspense } from 'react';
import { findUsersToAssign, getOrgRoleWithUsers } from './.server/role.queries';

export { roleEditActionFunction as action } from './.server/role.queries';

export const loader = async ({ params, request }: LoaderFunctionArgs) => {
  const { currentOrg: userOrg, id: userId } = await requireUserAsObject(
    request,
    {
      permissions: Attribute.ORGROLES_READWRITE,
    }
  );

  const { roleId: orgRoleUuid } = params;
  if (!orgRoleUuid) throw new Error('roleId is required');

  const searchParams = new URL(request.url).searchParams;

  const { orgBeingViewed: org } = await getOrgBeingViewed({
    defaultOrgId: userOrg.uuid,
    params,
    searchParams,
  });

  const orgRole = getOrgRoleWithUsers(orgRoleUuid);

  const users = findUsersToAssign(org, orgRole, userId);

  return defer({
    orgRole,
    users,
    currentUser: userId,
  });
};

const EditRoleDefaultPage = () => {
  const { users, orgRole, currentUser } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <div className="mb-5">
        <Suspense fallback={<AssignUserModalSkeleton />}>
          <Await resolve={orgRole}>
            {(orgRole) => (
              <>
                <Modal.Title className="pt-4 pb-3 px-5 bg-gradient-to-r from-neutral-100 to-neutral-50 border-b border-b-1 border-b-neutral-200/80">
                  Assigning Users to:{' '}
                  <p className="text-lg h-6 truncate">{orgRole.role.name}</p>
                </Modal.Title>
                <input type="hidden" name="orgRoleId" value={orgRole.uuid} />
                <input type="hidden" name="_action" value="edit" />
                <section className="w-full space-y-4 divide-neutral-200/80 px-4">
                  <div className="">
                    <h3 className="mb-2 font-semibold text-left w-full">
                      Assign a User
                    </h3>
                    <Suspense fallback={<AssignUserSelectSkeleton />}>
                      <Await resolve={users}>
                        {(usersJson) => (
                          <>
                            {usersJson.length > 0 && (
                              <>
                                <AssignUserSelect
                                  users={usersJson}
                                  orgRoleId={orgRole.uuid}/>
                              </>
                            )}
                            {usersJson.length === 0 && (
                              <p className="mt-2 p-2 text-sm border-t border-t-1">
                                No users available to assign.
                              </p>
                            )}
                          </>
                        )}
                      </Await>
                    </Suspense>
                  </div>
                  <div className="">
                    <h3 className="text-left font-semibold w-full pb-2 border-b border-b-1 border-b-neutral-200/80 ">
                      Assigned Users
                    </h3>
                    {orgRole.users.length === 0 && (
                      <p className="m-2 text-sm">
                        No users have been assigned.
                      </p>
                    )}
                    {orgRole?.users.length > 0 && (
                      <UserList
                        users={orgRole?.users}
                        currentUser={currentUser}
                        orgRoleId={orgRole.uuid}/>
                    )}
                  </div>
                </section>
              </>
            )}
          </Await>
        </Suspense>
      </div>
      <Modal.Buttons type="button" hideCancelBtn={true} variant="outline">
        Done
      </Modal.Buttons>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditRoleDefaultPage;
