import { TrashIcon } from '@heroicons/react/24/outline';
import { useFetcher } from '@remix-run/react';

type UserListItemProps = {
  user: { id: string; name: string; };
  currentUser: string;
  orgRoleId: string;
};

export function UserListItem( { user, currentUser, orgRoleId }: UserListItemProps ) {
  const deleteFetcher = useFetcher();

  return (
    <li className="flex justify-between items-center px-5 h-12 even:bg-neutral-100">
      <p className="truncate h-6">{ user.name }</p>
      <div className="whitespace-nowrap w-10 flex items-center align-middle justify-items-center justify-center">
        { user.id === currentUser && (
          <span className="font-normal text-xs rounded-xl bg-blue-500 text-white px-3 py-[0.15rem] self-center">
            You
          </span>
        ) }
        { user.id !== currentUser && (
          <deleteFetcher.Form method="POST">
            <input type="hidden" name="userId" value={ user.id }/>
            <input type="hidden" name="orgRoleId" value={ orgRoleId }/>
            { deleteFetcher.state !== 'idle' && (
              <>
                <svg
                  className="animate-spin -ml-1 size-5 items-center rounded-md bg-white text-sm font-semibold text-gray-900 shadow-sm sm:mt-0 sm:w-auto"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24">
                  <circle
                    className="opacity-25"
                    cx="12"
                    cy="12"
                    r="10"
                    stroke="currentColor"
                    stroke-width="4"></circle>
                  <path
                    className="opacity-75"
                    fill="currentColor"
                    d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
              </>
            ) }
            { deleteFetcher.state === 'idle' && (
              <button
                name="_action"
                value="delete"
                disabled={ deleteFetcher.state !== 'idle' }
                className="inline-flex text-red-700 p-1 items-center cursor-pointer w-full justify-center drop-shadow-none rounded-md text-sm font-semibold hover:bg-red-200 hover:drop-shadow sm:mt-0 sm:w-auto">
                <>
                  <TrashIcon
                    className="size-6"
                    aria-hidden="true"></TrashIcon>
                </>
              </button>
            ) }
          </deleteFetcher.Form>
        ) }
      </div>
    </li>
  );
}