import {
  Combobox,
  ComboboxButton,
  ComboboxInput,
  ComboboxOption,
  ComboboxOptions,
  Transition,
} from '@headlessui/react';
import { ChevronDownIcon, ChevronUpIcon } from '@heroicons/react/24/solid';
import { UserRank } from '@lib/constants';
import type { OrgRoleAssignedUsers } from '@lib/types';
import React, { useEffect, useState } from 'react';
import type { Rank } from '@prisma/client';
import { type FetcherWithComponents } from '@remix-run/react';

type UserSelectProps = {
  dropdownOptions: OrgRoleAssignedUsers[];
  formName: string;
  className?: string;
  fetcher: FetcherWithComponents<unknown>;
  isLoading: boolean;
};

export function UserSelect({
  dropdownOptions,
  formName,
  className,
  fetcher,
  isLoading,
}: UserSelectProps) {
  const [selectedOption, setSelectedOption] =
    useState<OrgRoleAssignedUsers | null>(null);
  const [query, setQuery] = useState('');

  useEffect(() => {
    if (fetcher.state === 'submitting') {
      setSelectedOption(null);
      setQuery('');
    }
  }, [fetcher]);

  const filteredUsers =
    query !== ''
      ? dropdownOptions.filter((user) =>
        user.name.toLowerCase().includes(query.toLowerCase())
      )
      : dropdownOptions;

  function displayValue(user: { id: string; name: string; rank?: Rank }) {
    // if (query === '' && selectedOption.id === '') return initialValue.name;
    if (!user || !selectedOption) return '';
    if (user.rank) return `${UserRank[user.rank].abb} ${user.name}`;

    return user.name;
  }

  return (
    <div className={`${className}`}>
      <Combobox
        immediate
        disabled={fetcher.state !== 'idle' || isLoading}
        value={selectedOption}
        onChange={setSelectedOption}
        virtual={{ options: filteredUsers }}
        onClose={() => setQuery('')}
        name={formName}>
        {({ open }) => (
          <div className="relative">
            <ComboboxInput
              placeholder="Please select a member"
              displayValue={displayValue}
              onChange={(event) => setQuery(event.target.value)}
              className="relative w-full bg-white py-1.5 pl-3 pr-10 text-left text-slate-900 focus:outline-none focus:ring-indigo-500/20 sm:text-sm sm:leading-6 rounded-md border-slate-300 text-xs shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50"></ComboboxInput>
            <ComboboxButton className="group absolute inset-y-0 right-0 px-2.5 border-l border-l-slate-300">
              {({ open }) => (
                <>
                  {open && (
                    <ChevronUpIcon
                      className="size-4 fill-slate-900"
                      aria-hidden="true"/>
                  )}
                  {!open && (
                    <ChevronDownIcon
                      className="size-4 fill-slate-900"
                      aria-hidden="true"/>
                  )}
                </>
              )}
            </ComboboxButton>
            <Transition
              show={open}
              enter="transition duration-100 ease-out"
              enterFrom="transform scale-95 opacity-0"
              enterTo="transform scale-100 opacity-100"
              leave="transition duration-75 ease-out"
              leaveFrom="transform scale-100 opacity-100"
              leaveTo="transform scale-95 opacity-0">
              <ComboboxOptions
                anchor={{ to: 'bottom start' }}
                className="[--anchor-max-height:45vh] absolute z-10 w-[30rem] mt-1 overflow-auto rounded-md bg-white py-1 text-base shadow-lg ring-1 ring-black/5 focus:outline-none sm:text-sm">
                {({ option: user }) => (
                  <ComboboxOption
                    key={`${user.id}-combo-option`}
                    className="cursor-default w-full select-none py-2 pl-10 pr-4 text-gray-900 hover:cursor-pointer data-[active]:bg-blue-300 data-[active]:bg-opacity-60 data-[selected]:bg-zinc-800/20"
                    value={user}>
                    <span className={`block truncate 'font-normal'`}>
                      {UserRank[user.rank as Rank].abb} {user.name}
                    </span>
                  </ComboboxOption>
                )}
              </ComboboxOptions>
            </Transition>
          </div>
        )}
      </Combobox>
    </div>
  );
}
