import UserSelect from '@components/OrgRoleUserSelect';
import { PlusIcon } from '@heroicons/react/24/solid';
import { useFetcher, useFetchers } from '@remix-run/react';
import { useMemo } from 'react';

type AssignUserSelectProps = {
  orgRoleId: string;
  users: {id:string; name:string;}[]
}

export function AssignUserSelect({ orgRoleId, users }: AssignUserSelectProps) {
  const addUserFetcher = useFetcher();
  const fetchers = useFetchers();
  const loadingState = useMemo(() => {
    return fetchers.some((fetcher) => fetcher.state !== 'idle');
  }, [fetchers]);

  return (
    <addUserFetcher.Form method="POST" className="flex gap-2 items-center">
      <input type="hidden" name="orgRoleId" value={orgRoleId} />
      <UserSelect
        dropdownOptions={users}
        isLoading={loadingState}
        formName={'newUser'}
        fetcher={addUserFetcher}
        className="flex-grow"/>
      <button
        name="_action"
        value="addUser"
        id="submitButton"
        type="submit"
        className="inline-flex w-32 px-3 py-5 leading-6 h-8 w- justify-center items-center rounded-md bg-green-600 text-white text-sm font-semibold  shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-green-500">
        {addUserFetcher.state !== 'idle' && (
          <>
            <svg
              className="animate-spin -ml-1 mr-2 size-5"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24">
              <circle
                className="opacity-25"
                cx="12"
                cy="12"
                r="10"
                stroke="currentColor"
                strokeWidth="4"></circle>
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            Adding
          </>
        )}
        {addUserFetcher.state === 'idle' && (
          <>
            Assign
          </>
        )}
      </button>
    </addUserFetcher.Form>
  );
}