import { UserListItem } from './UserListItem';

type UserListProps = {
  users: { id: string; name: string; }[];
  currentUser: string;
  orgRoleId: string;
}

export function UserList({ users, currentUser, orgRoleId }: UserListProps) {
  return (
    <>
      <ul className="w-full divide-y overflow-auto max-h-[60vh]">
        {users.map((item) => (
          <UserListItem
            key={item.id}
            currentUser={currentUser}
            user={item}
            orgRoleId={orgRoleId}/>
        ))}
      </ul>
    </>
  );
}